@use "sass:math";
.parent {
    padding: 68px 0;
}
.container {
    width: 95%;
    @include mq($sm) {
        width: math.div(10, 12) * 100%;
    }
    @include mq($md) {
        width: math.div(9, 12) * 100%;
        max-width: 576px;
    }
    @include mq($lg) {
        width: math.div(10, 12) * 100%;
        max-width: 923px;
    }
    margin-inline: auto;
}
a {
    text-decoration: none;
}
li {
    list-style: none;
}
button {
    cursor: pointer;
    user-select: none;
    &,
    &:active {
        border: none;
        outline: none;
    }
    &:active {
        @extend %on_click;
    }
}
.img.bg {
    @include before {
        @extend %center;
        background: var(--clr--cyan);
        border-radius: $radius;
        z-index: -1;
        width: 94.6%;
        padding-top: 94.6%;
    }
}
